import { App, Button, Space } from "antd"
import useAPIKey from "../../hooks/useAPIKey"
import axios from "axios";
import http from "../../utils/http";

 function About() {

    const {message} = App.useApp();
    // variables
    const url = 'http://localhost:8080/login'

    // API KEY
    const [api, setAPI] = useAPIKey();


    // test on axios
    const axCall = ()=> {
        // axios.post(url,{username:"solar", password:"abc"}).then(axCallBack).catch(()=> message.error("fail to call"));

        http.post(url,{username:"solar", password:"abcd1234"}).then(axCallBack).catch(()=> message.error("fail to call"));

        console.log("after axios" );
    }

    const mPromise = ()=>{
        return new Promise(
            (resolve, reject) => {
                console.log("start");
                resolve("resolve");
                reject("reject");
    
                console.log("end");
            }
        );

    }
    

    // test on promise
    const pmCall = ()=>{
        console.log("pmCall");

        mPromise().then(msg=> console.log(msg));

        console.log("pmcall end")
    }

    const axCallBack = (data)=>{
        console.log("data: ", data);
    }

    return (
        <Space>


            <Button onClick={()=>setAPI('Orage')}>set to oranger</Button>

            <Button onClick={axCall}>call API</Button>
            <Button onClick={pmCall}>call Promise</Button>

            
        </Space>

        
    )
}



export default About